import React from 'react';
import Items from './Items';

export default function EmojisContainer(props) {
  console.log(props);
  return (
    <div
      style={{
        display: 'flex',
        flexWrap: 'wrap',
        width: '80%',
        margin: '0 auto',
      }}
    >
      {props.emojiList.map((emoji) => {
        return (
          <Items
            key={emoji.title}
            symbol={emoji.symbol}
            keywords={emoji.keywords}
          />
        );
      })}
      ;
    </div>
  );
}
