import React, { useState } from 'react';
import Header from './components/Header';
import EmojisContainer from './components/EmojisContainer';
import data from './json/emoji.json';

function App() {
  const [emojiList, setEmojiList] = useState(data);

  const searchEmojis = (e) => {
    console.log(e.target.value);
    let searchedEmojis = data.filter((emoji) =>
      emoji.keywords.includes(e.target.value)
    );
    setEmojiList(searchedEmojis);
  };
  return (
    <div className="App">
      <Header />
      <div style={{ textAlign: 'center' }}>
        <input
          type="text"
          name="search"
          onChange={searchEmojis}
          style={{
            display: 'inline-block',
            borderRadius: '10px',
            width: '500px',
            padding: '10px',
            outline: 'none',
          }}
        />
      </div>
      <EmojisContainer emojiList={emojiList} />
    </div>
  );
}

export default App;
