import React from 'react';
import ReactClipboard from 'react-clipboard-simple';

export default function Items(props) {
  return (
    <ReactClipboard keyword={props.symbol}>
      <div
        style={{
          width: '200px',
          height: '200px',
          display: 'grid',
          placeItems: 'center',
          border: '1px solid grey',
          boxShadow: '5px 5px 5px grey',
          borderRadius: '20px',
          margin: '20px',
          fontSize: '25px',
        }}
      >
        <h1>{props.symbol}</h1>
      </div>
    </ReactClipboard>
  );
}
